import { Component, OnInit,ViewChild } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';
import { ImageResult } from 'ng2-imageupload';
import { Router } from '@angular/router';

@Component({
  selector: 'app-addupdate-item',
  templateUrl: './addupdate-item.component.html',
  styleUrls: ['./addupdate-item.component.css']
})
export class AddupdateItemComponent implements OnInit {
 @ViewChild('myInput')
      myInputVariable: any;
  
    public object:any = {
        name:'',
        address:'',
        city:'',
        postal_code:null,
        email:'',
        mobile:null,
        website:'',
        about_us:'',
        image:'',
        logo:'',
        lat:1,
        businessType:'doctor',
        medical:null,
        recreation:null,
        lng:1,
        isFeatured:false,
        scheduler: []
    };
    public _host      = environment.config.BASE_URL;
    public itemID = ''
    public roles:any;
    public  days = ['Monday','Tuesday','Wednesday','Thursday','Friday','Saturday','Sunday']
    public showMyMenu = true;
  
    constructor(
      private _router:Router,
      private _sharedService: SharedService) { }

    ngOnInit() {
      this.fetch();
      this.roles = this._sharedService.getRoles();
    }
  
    fetch() {
      this._sharedService.get('getDispensary').then((res: any) => {
          if(res.success) {
              this.object = res.data.key; 
              this.itemID = this.object.id;
          } else {
            this.showMyMenu = false;
            if(this.roles != 'B') this.addScheduler();
          }
      },(error) => { 
         if(this.roles != 'B') this.addScheduler();
          this.showMyMenu = false;
      });
    }

    addScheduler() {
      this.days.forEach((key)=>{
                let obj = {
                day:key,
                startTime:'9:00AM',
                closeTime:'5:00PM'
            }
            this.object.scheduler.push(obj)
            
        })
    }

    save() {
      let data = JSON.parse(JSON.stringify(this.object));
      this._sharedService.loader('show');
       if(this.itemID) {
         let path = 'edit_dispensary/' + this.itemID;
           this._sharedService.put(data,path).then((res: any) => {
               this._sharedService.loader('hide');
              if(res.success) {
                let dType = this.object.businessType == 'doctor' ? 'Doctor' : this.object.businessType == 'dispensary' ? 'Dispensary' :'Brand';
                let message = dType +  ' Updated Successfully';
                this._sharedService.showAlert(message,'alert-success');
              } else {
                this._sharedService.showAlert(res.error.message,'alert-danger')
              }
          },(error) => { });
       } else {
         this._sharedService.post(data,'add_dispensary').then((res: any) => {
             this._sharedService.loader('hide');
            if(res.success) {
              let dType = this.object.businessType == 'doctor' ? 'Doctor' : this.object.businessType == 'dispensary' ? 'Dispensary' :'Brand';
              let message = dType +  ' Created Successfully';
              this._sharedService.showAlert(message,'alert-success')
              this._router.navigate(["/item-dashboard/my-menu"]);
              this.showMyMenu = true;      
            } else {
              this._sharedService.showAlert(res.error.message,'alert-danger')
            }
        },(error) => { });
       }
    }

    uploadImage(imageResult: ImageResult) {
        let object = {
            data:imageResult.dataURL,
            type:'dispensary'
        }
        this.myInputVariable.nativeElement.value = "";
        this._sharedService.loader('show');
        this._sharedService.post(object,'upload').then( res => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.object.image = res.data.fullPath;
            } else {
            }
        },err => { });
    }

}



