import { Component, OnInit,ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';
import { ImageResult } from 'ng2-imageupload';

@Component({
  selector: 'app-edit-product',
  templateUrl: './edit-product.component.html',
  styleUrls: ['./edit-product.component.css']
})
export class EditProductComponent implements OnInit {
    @ViewChild('myInput')
    myInputVariable: any;

    _host      = environment.config.BASE_URL;
    obj        = {
      "name": "",
      "pre_roll": "",
      "Eighth": "",
      "quarter": "",
      "grams":"",
      "half": "",
      "ounce": "",
      "category_id":'',
      "details": "",
      "thc": "",
      "image": "",
      "isDeleted": false,
      "cbd": "",
      "status": "active",
      "brand_name": "",
      "product_id": ""
    }
    categories = []
    dispenaryID:any
    roles:any;
    
    constructor(
        private _router : Router,
		private _route: ActivatedRoute,        
        private _sharedService: SharedService) { }

    ngOnInit() {
      this.fetch();
      this.roles = this._sharedService.getRoles();
    }
  
    fetch() {
    	let options = {
    		id: this._route.snapshot.params['id']
    	}
      this._sharedService.loader('show');
      this._sharedService.queryParams('getItemProduct',options).then((res: any) => {
            this._sharedService.loader('hide');
            if(res.success) {
               this.obj = res.data.key[0]; 
               this.getAllCategories();
            }
      },(error) => { });
    }

    getAllCategories() {
      this._sharedService.get('categoryList').then(res => {
            if(res.success) {
                this.categories = res.data.category;
            } else {
            }
        },err => {  });
    }

    save() {
        let data              = JSON.parse(JSON.stringify(this.obj));
        this._sharedService.loader('show');
        this._sharedService.updateProducts(data.id,data).then(res => {
            this._sharedService.loader('hide');
            if(res.success) {
                this._router.navigate(['item-dashboard/my-menu']);
            } else {
            }
        },err => {  });
    }

    uploadImage(imageResult: ImageResult) {
        let object = {
            data:imageResult.dataURL,
            type:'product'
        }
        this.myInputVariable.nativeElement.value = "";
        this._sharedService.loader('show');
        this._sharedService.post(object,'upload').then( res => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.obj.image = res.data.fullPath;
            } else {
            }
        },err => { });
    }

}



