import { Component, OnInit,ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';
import { ImageResult } from 'ng2-imageupload';

@Component({
  selector: 'app-my-menu',
  templateUrl: './my-menu.component.html',
  styleUrls: ['./my-menu.component.css']
})
export class MyMenuComponent implements OnInit {
  @ViewChild('myInput')
        myInputVariable: any;

  public _host      = environment.config.BASE_URL;
  products   = []
    categories = []
    dispenaryID:any
    public roles:any;
  constructor(
      private _router : Router,
      private _sharedService: SharedService) { }

    ngOnInit() {
      // this.fetch();
      this.addObject();
      this.getAllCategories();
      this.roles = this._sharedService.getRoles();
    }
  
    fetch() {
      // this._sharedService.loader('show');
      // this._sharedService.get('AllProductByUser').then((res: any) => {
      //   this._sharedService.loader('hide');
      //     if(res.success) {
      //         this.products = res.data.key ? res.data.key : []; 
              // this.getAllCategories()
      //     } else {
      //     }
      // },(error) => { this._sharedService.loader('hide'); });
      // this._sharedService.get('getDispensary').then((res: any) => {
      //     if(res.success) {
      //         let object = res.data.key; 
      //         this.dispenaryID = object.id;
      //     } else {
      //     }
      // },(error) => { });
    }

    getAllCategories() {
      this._sharedService.get('categoryList').then(res => {
            if(res.success) {
                this.categories = res.data.category;
                // if(this.products.length==0) this.addObject();
                if(this.categories.length > 0) this.products[0].category_id = this.categories[0]['id'];
            } else {
            }
        },err => {  });
    }

    save() {
        let arrayObj              = JSON.parse(JSON.stringify(this.products));
        arrayObj.forEach((obj)=>{
            obj.dispensary_id = this.dispenaryID;
        })
        let data = {data:arrayObj};
        this._sharedService.loader('show');
        this._sharedService.post(data,'add_item_product').then(res => {
            this._sharedService.loader('hide');
            if(res.success) {
                this._router.navigate(['item-dashboard/my-menu']);
            } else {
            }
        },err => {  });
    }

    addObject() {
        let obj = {
          "name": "",
          "pre_roll": "",
          "Eighth": "",
          "quarter": "",
          "grams":"",
          "half": "",
          "ounce": "",
          "category_id": this.categories.length > 0 ? this.categories[0]['id'] : '',
          "details": "",
          "thc": "",
          "image": "",
          "isDeleted": false,
          "cbd": "",
          "status": "active",
          "brand_name": "",
          "product_id": ""
        }
        this.products.push(obj);
    }

    uploadImage(imageResult: ImageResult,index) {
        let object = {
            data:imageResult.dataURL,
            type:'product'
        }
        this.myInputVariable.nativeElement.value = "";
        this._sharedService.loader('show');
        this._sharedService.post(object,'upload').then( res => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.products[index].image = res.data.fullPath;
            } else {
            }
        },err => { });
    }

}



